import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
export default function Item(props) {
  const [state, setState] = useState({
    isOpenLesson: false,
  });

  const { isOpenLesson } = state;

  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <>
      <tr className="hover-product" style={{
       border :  isOpenLesson === true ? "1px solid #ebe2e2" : null
      }}>
        <td>
          <button
            style={{ maxWidth: "30px" }}
            className="btn-not-background"
            onClick={() => {
              setState({ ...state, isOpenLesson: !isOpenLesson });
            }}
          >
            {" "}
            <i
              class={`fas fa-angle-double-${
                isOpenLesson === true ? "down" : "right"
              }`}
            ></i>
          </button>
        </td>
        <td
          style={{ maxWidth: "200px" }}
          onClick={props.handleUpdate}
          data-toggle="modal"
          data-target="#modalUID"
        >
          <div style={{ color: "blue" }}>Bài Test 1</div>
        </td>
        <td>Bài học A</td>
        <td>Nguyễn Văn A</td>
        <td style={{ color: "green" }}>80%</td>

        <td className="three-btn-group">
        <Link
            to={"/train/question"}
            className="btn btn-success btn-sm show"
          >
            <i className="fa fa-plus" /> Tạo câu hỏi
          </Link>
          <button
            name="action"
            onClick={props.showListDoc}
            data-toggle="modal"
            data-target="#modalUID"
            className="btn btn-primary btn-sm show"
          >
            <i className="fa fa-file" /> Xem tài liệu
          </button>

          <button
            name="action"
            onClick={props.handleDelete}
            data-toggle="modal"
            data-target="#modalUID"
            className="btn btn-danger btn-sm show"
          >
            <i className="fa fa-trash" /> Xóa
          </button>
        </td>
      </tr>
      <tr className={`collaspe-lesson ${isOpenLesson == true ? "" : "hide"}`}>
        <td
          style={{
            background: "beige",
          }}
        ></td>
        <td
          colSpan={5}
          style={{
            border: "1px solid #dcd2d2",
            padding: "4px",
            marginTop: "5px",
          }}
        >
          {
            <>
              <div className="total">- Tỉ lệ đạt yêu cầu: <span className="total-number">100%</span></div>
              <div className="total">- Tỉ lệ đạt giỏi trở lên: <span className="total-number">100%</span></div>
              <div className="total">- Tỉ lệ đạt đạt xuất sắc: <span className="total-number">100%</span></div>
              <div className="total">- Xếp loại: <span className="total-number">14/20</span ></div>
            </>
          }
        </td>
      </tr>
    </>
  );
}
