import React, { useEffect, useState } from "react";

export default function ModalDocs() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;


  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <div className="list-docs" style={{ display: "flex", flexDirection: "column" }}>

      <div className="item">
        <span >1. </span>
        <a href="#" style={{ textDecoration: "underline", fontStyle: "oblique" }} >Tài liệu hướng dẫn sử dụng sách giáo khoa...</a>
      </div>

      <div className="item">
        <span >2. </span>
        <a href="#" style={{ textDecoration: "underline", fontStyle: "oblique" }} >Tài liệu hướng dẫn sử dụng sách giáo khoa...</a>
      </div>
    </div>
  );
}
