import React, { useEffect, useState } from "react";

export default function ModalCreate() {
  const [state, setState] = useState({
    name: "",
  });

  const { name } = state;


  function onChange(e) {
    var { name, value } = e.target;
    setState({ ...state, [name]: value });
  }

  return (
    <form role="form" >
      <div class="form-group">
        <label for="product_name">Tên bài Test</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Tên bài học liên quan</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>
      <div class="form-group">
        <label for="product_name">Người phụ trách</label>
        <select
                          name="type_schedule"
                          className="form-control"
                        >
                          <option value={0}>Nguyễn Văn A</option>
                    
                        </select>      </div>
      <div class="form-group">
        <label for="product_name">Số điểm yêu cầu</label>
        <input
          type="text"
          class="form-control"
          id="txtName_branch"
          placeholder="Nhập..."
          autocomplete="off"
        />
      </div>

    </form>
  );
}
